@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');
@import 'materialize';

*,
*:after,
*:before {
  box-sizing: border-box;
}

body {
  background: #e5e5e5;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

.converter {
  width: 940px;
  height: 314px;
  display: flex;
  background: #fff;
  padding: 60px 60px;
  justify-content: center;
  flex-direction: column;
  &-list {
    position: absolute;
    right: 0;
    top: 100%;
    z-index: 1;
    height: 149px;
    overflow: hidden;
    overflow-y: auto;
  }
  &-ticket {
    background: #f5f5f5;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(206, 205, 205);
    border-left: none;
    border-right: none;
    border-top: none;
    width: 170px;
    height: 50px;
    color: #626262;
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    cursor: pointer;
  }
  &-preloader {
    height: 3px;
    margin-top: 15px;
  }
  &-blocks {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
    &:nth-child(2n) {
      margin-bottom: 0;
    }
  }
  &-block,
  &-block-address {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 370px;
    height: 65px;
    background: #f5f5f5;
    padding: 10px 0;
    input {
      font-family: 'DM Sans', sans-serif;
      font-size: 20px;
      border: none;
      outline: none;
      background: transparent;
      border-right: 1px solid #dadada;
      width: 170px;
      &:nth-child(2n) {
        border: none;
        text-align: center;
        font-family: 'Raleway', sans-serif;
        font-size: 14px;
        cursor: pointer;
      }
    }

    .input-address {
      border: none;
      width: 450px;
    }
  }

  &-next {
    border: none;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 335px;
    background: #7aff77;
    color: #000000;
    font-weight: bold;
    font-size: 16px;
    cursor: pointer;
  }
  &-block-address {
    width: 450px;
    input {
      padding-left: 15px;
      font-size: 16px;
      color: #737373;
    }
  }
  .input-from {
    position: relative;
  }
}

.button-convert {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  svg {
    fill: rgb(145, 145, 145);
  }
  &:hover {
    svg {
      fill: #000;
    }
  }
}

.none {
  display: none;
}

.error-min,
.success {
  position: absolute;
  bottom: -19px;
  font-size: 13px;
  left: 0;
  color: tomato;
  font-weight: 500;
  font-family: 'Raleway', sans-serif;
  &__value {
    font-family: 'DM Sans', sans-serif;
  }
}

.success {
  color: rgb(82, 182, 57);
}

.progress {
  background: #7aff77;
  height: 2px;
}

@media (max-width: 720px) {
  body {
    background: none;
  }

  .container {
    height: 100vh;
  }

  .converter {
    width: 100%;
    padding: 10px 20px;
    background: none;
    &-list {
      right: 23%;
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
    }
    &-blocks {
      flex-direction: column;
      justify-content: space-between;
    }
    &-block {
      width: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 5px 0;
      height: 70px;
      border-radius: 15px;
      input {
        border-right: none;
        border-bottom: 1px solid #333;
        margin-bottom: 5px;
        text-align: center;
        &:nth-child(2n) {
          margin-bottom: 0;
        }
      }
    }
    .button-convert {
      margin: 10px 0;
    }
    &-next {
      width: 100%;
      margin-top: 20px;
      height: 40px;
      border-radius: 15px;
    }
    &-block-address {
      width: 100%;
      border-radius: 15px;
    }
  }

  .error-min {
    bottom: 80px;
  }
  .success {
    bottom: 80px;
  }
}
